import { useState } from "react";
import { DataTable } from "../../components/DataTable";
import { Badge } from "../../components/ui/badge";
import { Button } from "../../components/ui/button";
import { Card, CardContent } from "../../components/ui/card";
import { CheckCircle, Mail, UserPlus, AlertTriangle } from "lucide-react";
import { getSafeAlerts } from "../../lib/safe-mock-data";
import { toast } from "sonner";

export function Alerts() {
  const [statusFilter, setStatusFilter] = useState("active");
  const [priorityFilter, setPriorityFilter] = useState("all");

  const filteredAlerts = getSafeAlerts().filter((alert) => {
    const matchesStatus =
      statusFilter === "all" || alert.status === statusFilter;
    const matchesPriority =
      priorityFilter === "all" || alert.priority === priorityFilter;
    return matchesStatus && matchesPriority;
  });

  const getPriorityBadge = (priority: string) => {
    const variants: Record<string, any> = {
      high: { variant: "destructive", label: "Cao" },
      medium: { variant: "secondary", label: "Trung bnh" },
      low: { variant: "outline", label: "Thp" },
    };
    const config = variants[priority] || {
      variant: "secondary",
      label: priority,
    };
    return <Badge variant={config.variant}>{config.label}</Badge>;
  };

  const getTypeLabel = (type: string) => {
    const labels: Record<string, string> = {
      test_expiring: "Xt nghim ht hn",
      screening_incomplete: "Sng lc cha xong",
      unsigned_consent: "Cha k cam kt",
      payment_pending: "Ch thanh ton",
    };
    return labels[type] || type;
  };

  const handleMarkResolved = (alertId: string) => {
    toast.success(" nh du cnh bo  x l");
  };

  const handleSendReminder = (alert: any) => {
    toast.success(` gi nhc nh n ${alert.donorName}`);
  };

  const columns = [
    {
      key: "priority",
      header: "Mc ",
      render: (alert: any) => getPriorityBadge(alert.priority),
    },
    {
      key: "type",
      header: "Loi",
      render: (alert: any) => getTypeLabel(alert.type),
    },
    {
      key: "donor",
      header: "Donor",
      render: (alert: any) => (
        <div>
          <p>{alert.donorName}</p>
          <p className="text-muted-foreground">{alert.donorId}</p>
        </div>
      ),
    },
    {
      key: "message",
      header: "Thng bo",
    },
    {
      key: "createdAt",
      header: "Ngy to",
    },
    {
      key: "actions",
      header: "Thao tc",
      render: (alert: any) => (
        <div className="flex gap-2">
          {alert.status === "active" && (
            <>
              <Button
                variant="outline"
                size="sm"
                onClick={() => handleMarkResolved(alert.id)}
              >
                <CheckCircle className="h-4 w-4 mr-1" />
                 x l
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={() => handleSendReminder(alert)}
              >
                <Mail className="h-4 w-4 mr-1" />
                Nhc nh
              </Button>
              <Button variant="outline" size="sm">
                <UserPlus className="h-4 w-4 mr-1" />
                Giao vic
              </Button>
            </>
          )}
        </div>
      ),
    },
  ];

  const activeCount = getSafeAlerts().filter(
    (a) => a.status === "active"
  ).length;
  const highPriorityCount = getSafeAlerts().filter(
    (a) => a.status === "active" && a.priority === "high"
  ).length;
  const resolvedCount = getSafeAlerts().filter(
    (a) => a.status === "resolved"
  ).length;

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div>
        <h1>Qun l cnh bo</h1>
        <p className="text-muted-foreground">
          Theo di v x l cc cnh bo h thng
        </p>
      </div>

      {/* Summary */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-muted-foreground">Cnh bo ang hot ng</p>
                <h2>{activeCount}</h2>
              </div>
              <AlertTriangle className="h-8 w-8 text-yellow-600" />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-muted-foreground">u tin cao</p>
                <h2>{highPriorityCount}</h2>
              </div>
              <AlertTriangle className="h-8 w-8 text-destructive" />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-muted-foreground"> x l</p>
                <h2>{resolvedCount}</h2>
              </div>
              <CheckCircle className="h-8 w-8 text-green-600" />
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <div className="flex gap-4">
        <div className="flex gap-2">
          <Button
            variant={statusFilter === "active" ? "default" : "outline"}
            onClick={() => setStatusFilter("active")}
          >
            ang hot ng
          </Button>
          <Button
            variant={statusFilter === "resolved" ? "default" : "outline"}
            onClick={() => setStatusFilter("resolved")}
          >
             x l
          </Button>
          <Button
            variant={statusFilter === "all" ? "default" : "outline"}
            onClick={() => setStatusFilter("all")}
          >
            Tt c
          </Button>
        </div>
        <div className="flex gap-2">
          <Button
            variant={priorityFilter === "high" ? "destructive" : "outline"}
            onClick={() =>
              setPriorityFilter(priorityFilter === "high" ? "all" : "high")
            }
          >
            Cao
          </Button>
          <Button
            variant={priorityFilter === "medium" ? "default" : "outline"}
            onClick={() =>
              setPriorityFilter(priorityFilter === "medium" ? "all" : "medium")
            }
          >
            Trung bnh
          </Button>
          <Button
            variant={priorityFilter === "low" ? "secondary" : "outline"}
            onClick={() =>
              setPriorityFilter(priorityFilter === "low" ? "all" : "low")
            }
          >
            Thp
          </Button>
        </div>
      </div>

      {/* Table */}
      <DataTable
        data={filteredAlerts}
        columns={columns}
        emptyMessage="Khng c cnh bo no"
      />
    </div>
  );
}
export default Alerts;
