// Mock data for Mother Milk Donation Management System

export interface Donor {
  id: string;
  name: string;
  dob: string;
  phone: string;
  ehrId: string;
  status: 'pending' | 'interviewed' | 'needs_tests' | 'approved' | 'rejected';
  registeredAt: string;
  email: string;
  address: string;
  emergencyContact: string;
}

export interface Appointment {
  id: string;
  donorId: string;
  donorName: string;
  type: 'screening' | 'donation';
  date: string;
  time: string;
  staff: string;
  status: 'scheduled' | 'arrived' | 'completed' | 'failed';
  notes?: string;
}

export interface Donation {
  id: string;
  donorId: string;
  donorName: string;
  date: string;
  volume: number;
  containers: number;
  staff: string;
  points: number;
  notes?: string;
  healthStatus: 'good' | 'bad';
}

export interface EHRTest {
  id: string;
  donorId: string;
  donorName: string;
  testType: 'HIV' | 'Hepatitis B' | 'Hepatitis C' | 'Syphilis';
  result: 'negative' | 'positive' | 'pending';
  date: string;
  lab: string;
  validity: 'valid' | 'expired' | 'expiring_soon';
  expiryDate: string;
}

export interface Alert {
  id: string;
  type: 'test_expiring' | 'screening_incomplete' | 'unsigned_consent' | 'payment_pending';
  donorId: string;
  donorName: string;
  message: string;
  priority: 'high' | 'medium' | 'low';
  createdAt: string;
  status: 'active' | 'resolved';
}

export interface Payment {
  id: string;
  donorId: string;
  donorName: string;
  amount: number;
  status: 'pending' | 'transferred' | 'failed';
  requestedAt: string;
  transferredAt?: string;
  receiptUrl?: string;
}

export interface RewardRule {
  id: string;
  name: string;
  volumeThreshold: number;
  points: number;
  effectiveFrom: string;
  effectiveTo?: string;
  active: boolean;
}

