import {
  Sidebar,
  SidebarContent,
  SidebarGroup,
  SidebarGroupContent,
  SidebarGroupLabel,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  SidebarHeader,
  SidebarFooter,
} from "./ui/sidebar";
import {
  LayoutDashboard,
  Users,
  Calendar,
  ClipboardList,
  Droplet,
  TestTube,
  Bell,
  CreditCard,
  LogOut,
} from "lucide-react";
import { useLocation, Link, useNavigate } from "react-router-dom";
import { useAuth } from "../context/AuthContext";

const menuItems = [
  {
    title: "Tng quan",
    items: [
      { title: "Dashboard", icon: LayoutDashboard, url: "/staff/dashboard" },
    ],
  },
  {
    title: "Qun l Donor",
    items: [
      { title: "Danh sch Donor", icon: Users, url: "/staff/donors" },
      { title: "Lch hn", icon: Calendar, url: "/staff/appointments" },
      { title: "Sng lc", icon: ClipboardList, url: "/staff/screening" },
    ],
  },
  {
    title: "Hin sa",
    items: [
      { title: "Nht k hin sa", icon: Droplet, url: "/staff/donations" },
    ],
  },
  {
    title: "Y t & H tr",
    items: [
      { title: "Xt nghim EHR", icon: TestTube, url: "/staff/ehr-tests" },
      { title: "Cnh bo", icon: Bell, url: "/staff/alerts" },
      { title: "Thanh ton", icon: CreditCard, url: "/staff/payments" },
    ],
  },
];

export function StaffSidebar() {
  const location = useLocation();
  const navigate = useNavigate();
  const { logout, user } = useAuth();

  const handleLogout = () => {
    logout();
    navigate("/"); // Navigate to homepage after logout
  };

  return (
    <Sidebar>
      <SidebarHeader className="border-b border-gray-200 px-6 py-4 h-[73px] flex items-center">
        <div className="flex items-center gap-2">
          <Droplet className="h-8 w-8 text-primary" />
          <div>
            <h2 className="font-semibold text-lg">MilkBank Staff</h2>
            <p className="text-xs text-muted-foreground">
              {user?.name || "Medical Staff"}
            </p>
          </div>
        </div>
      </SidebarHeader>
      <SidebarContent>
        {menuItems.map((group) => (
          <SidebarGroup key={group.title}>
            <SidebarGroupLabel>{group.title}</SidebarGroupLabel>
            <SidebarGroupContent>
              <SidebarMenu>
                {group.items.map((item) => (
                  <SidebarMenuItem key={item.title}>
                    <SidebarMenuButton
                      asChild
                      isActive={location.pathname === item.url}
                    >
                      <Link to={item.url}>
                        <item.icon className="h-4 w-4" />
                        <span>{item.title}</span>
                      </Link>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                ))}
              </SidebarMenu>
            </SidebarGroupContent>
          </SidebarGroup>
        ))}
      </SidebarContent>
      <SidebarFooter className="border-t border-sidebar-border p-4">
        <button
          onClick={handleLogout}
          className="flex w-full items-center gap-2 rounded-md px-3 py-2 text-sm hover:bg-sidebar-accent hover:text-sidebar-accent-foreground"
        >
          <LogOut className="h-4 w-4" />
          <span>ng xut</span>
        </button>
      </SidebarFooter>
    </Sidebar>
  );
}
