import { useState } from "react";
import { DataTable } from "../../components/DataTable";
import { Badge } from "../../components/ui/badge";
import { Button } from "../../components/ui/button";
import { Card, CardContent } from "../../components/ui/card";
import { Input } from "../../components/ui/input";
import { Label } from "../../components/ui/label";
import { Upload, CheckCircle, Clock, XCircle } from "lucide-react";
import { getSafePayments } from "../../lib/safe-mock-data";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "../../components/ui/dialog";
import { toast } from "sonner";

export function Payments() {
  const [statusFilter, setStatusFilter] = useState("all");
  const [selectedPayment, setSelectedPayment] = useState<any>(null);
  const [isTransferModalOpen, setIsTransferModalOpen] = useState(false);
  const [receiptFile, setReceiptFile] = useState<File | null>(null);
  const [transferDate, setTransferDate] = useState("");

  const filteredPayments = getSafePayments().filter((payment) => {
    return statusFilter === "all" || payment.status === statusFilter;
  });

  const handleMarkTransferred = () => {
    if (!transferDate) {
      toast.error("Vui lng chn ngy chuyn khon");
      return;
    }

    toast.success(
      ` nh du  chuyn khon cho ${selectedPayment.donorName}`,
      {
        description: receiptFile ? "Bin lai  c ti ln" : "",
      }
    );

    setIsTransferModalOpen(false);
    setSelectedPayment(null);
    setReceiptFile(null);
    setTransferDate("");
  };

  const getStatusBadge = (status: string) => {
    const variants: Record<string, any> = {
      pending: { variant: "secondary", label: "Ch x l", icon: Clock },
      transferred: {
        variant: "default",
        label: " chuyn",
        icon: CheckCircle,
      },
      failed: { variant: "destructive", label: "Tht bi", icon: XCircle },
    };
    const config = variants[status] || {
      variant: "secondary",
      label: status,
      icon: Clock,
    };
    const Icon = config.icon;
    return (
      <Badge variant={config.variant} className="gap-1">
        <Icon className="h-3 w-3" />
        {config.label}
      </Badge>
    );
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("vi-VN", {
      style: "currency",
      currency: "VND",
    }).format(amount);
  };

  const columns = [
    {
      key: "id",
      header: "Payment ID",
    },
    {
      key: "donor",
      header: "Donor",
      render: (payment: any) => (
        <div>
          <p>{payment.donorName}</p>
          <p className="text-muted-foreground">{payment.donorId}</p>
        </div>
      ),
    },
    {
      key: "amount",
      header: "S tin",
      render: (payment: any) => formatCurrency(payment.amount),
    },
    {
      key: "requestedAt",
      header: "Ngy yu cu",
    },
    {
      key: "transferredAt",
      header: "Ngy chuyn",
      render: (payment: any) => payment.transferredAt || "-",
    },
    {
      key: "status",
      header: "Trng thi",
      render: (payment: any) => getStatusBadge(payment.status),
    },
    {
      key: "actions",
      header: "Thao tc",
      render: (payment: any) => (
        <div className="flex gap-2">
          {payment.status === "pending" && (
            <Button
              variant="default"
              size="sm"
              onClick={() => {
                setSelectedPayment(payment);
                setIsTransferModalOpen(true);
              }}
            >
              <CheckCircle className="h-4 w-4 mr-1" />
              nh du  chuyn
            </Button>
          )}
          {payment.status === "transferred" && payment.receiptUrl && (
            <Button variant="outline" size="sm">
              <Upload className="h-4 w-4 mr-1" />
              Xem bin lai
            </Button>
          )}
        </div>
      ),
    },
  ];

  const pendingCount = getSafePayments().filter(
    (p) => p.status === "pending"
  ).length;
  const transferredCount = getSafePayments().filter(
    (p) => p.status === "transferred"
  ).length;
  const totalPending = getSafePayments()
    .filter((p) => p.status === "pending")
    .reduce((sum, p) => sum + p.amount, 0);

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div>
        <h1>H tr thanh ton</h1>
        <p className="text-muted-foreground">
          Qun l thanh ton cho cc donor
        </p>
      </div>

      {/* Summary */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-muted-foreground">Ch x l</p>
                <h2>{pendingCount}</h2>
              </div>
              <Clock className="h-8 w-8 text-yellow-600" />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-muted-foreground"> chuyn</p>
                <h2>{transferredCount}</h2>
              </div>
              <CheckCircle className="h-8 w-8 text-green-600" />
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-muted-foreground">Tng ch thanh ton</p>
                <h2>{formatCurrency(totalPending)}</h2>
              </div>
              <Clock className="h-8 w-8 text-muted-foreground" />
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <div className="flex gap-2">
        <Button
          variant={statusFilter === "all" ? "default" : "outline"}
          onClick={() => setStatusFilter("all")}
        >
          Tt c
        </Button>
        <Button
          variant={statusFilter === "pending" ? "default" : "outline"}
          onClick={() => setStatusFilter("pending")}
        >
          Ch x l
        </Button>
        <Button
          variant={statusFilter === "transferred" ? "default" : "outline"}
          onClick={() => setStatusFilter("transferred")}
        >
           chuyn
        </Button>
        <Button
          variant={statusFilter === "failed" ? "default" : "outline"}
          onClick={() => setStatusFilter("failed")}
        >
          Tht bi
        </Button>
      </div>

      {/* Table */}
      <DataTable
        data={filteredPayments}
        columns={columns}
        emptyMessage="Khng c yu cu thanh ton no"
      />

      {/* Mark as Transferred Modal */}
      <Dialog open={isTransferModalOpen} onOpenChange={setIsTransferModalOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Xc nhn chuyn khon</DialogTitle>
            <DialogDescription>
              nh du  chuyn khon cho donor
              {selectedPayment && (
                <div className="mt-2 p-3 bg-muted rounded-md">
                  <p>Donor: {selectedPayment.donorName}</p>
                  <p>S tin: {formatCurrency(selectedPayment.amount)}</p>
                  <p>Payment ID: {selectedPayment.id}</p>
                </div>
              )}
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label htmlFor="transferDate">Ngy chuyn khon *</Label>
              <Input
                id="transferDate"
                type="date"
                value={transferDate}
                onChange={(e) => setTransferDate(e.target.value)}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="receipt">Ti ln bin lai (ty chn)</Label>
              <Input
                id="receipt"
                type="file"
                accept="image/*,.pdf"
                onChange={(e) => setReceiptFile(e.target.files?.[0] || null)}
              />
              {receiptFile && (
                <p className="text-sm text-muted-foreground">
                   chn: {receiptFile.name}
                </p>
              )}
            </div>
          </div>
          <DialogFooter>
            <Button
              variant="outline"
              onClick={() => {
                setIsTransferModalOpen(false);
                setSelectedPayment(null);
                setReceiptFile(null);
                setTransferDate("");
              }}
            >
              Hy
            </Button>
            <Button onClick={handleMarkTransferred}>Xc nhn</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
}
export default Payments;
